<template>
  <div class="slider">
        <swiper :options="swiperOptions">

          <swiper-slide>
            <div class="slider-list">
              <div class="mask3"></div>
              <div class="masked3"></div>
              <div class="reviews-details">
                <div class="reviews-details-top">
                  <div class="reviews-left-img">
                    <icon name="user-circle" scale="3"></icon>
                  </div>
                  <div class="reviews-name">
                    <p class="reviews-names">依然 </p>
                  </div>
                  <div class="report">
                    <i class="el-icon-more"></i>

                  </div>
                </div>
                <div class="reviews-details-bottoms">
                  <p>（萌游姬攻略组）暗影诗章WLD版本控鬼思路</p>
                  <p>暗影诗章（shadowverse）TW这是一套WLD版本初期,比较好用....</p>
                </div>
                <div class="reviews-details-img">
                  <el-row :gutter="10">
                    <el-col :xs="24" :sm="24" :md="12" :lg="12"><div class="img01"><img src="./2.jpg"></div></el-col>
                    <el-col :xs="24" :sm="24" :md="12" :lg="12"><div class="img01"><img src="./3.png"></div></el-col>
                    <el-col :xs="24" :sm="24" :md="12" :lg="12"><div class="img01"><img src="./4.png"></div></el-col>
                  </el-row>
                </div>
                <div class="browse">
                  <p>
                    <span><a href="javacript:void(0)"><icon name="eye"></icon></a>53</span>
                    <span><a href="javacript:void(0)"><icon name="comment"></icon></a>3</span>
                  </p>
                </div>

              </div>
            </div>
          </swiper-slide>
          
          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
          <div class="swiper-button-prev3" slot="button-prev"><icon name="angle-left" scale="5"></icon></div>
          <div class="swiper-button-next3" slot="button-next"><icon name="angle-right" scale="5"></icon></div>
        </swiper>
  </div>
</template>

<script>
  // import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {

    data () {
      return {
        swiperOptions: {

          pagination: '.swiper-pagination',

          nextButton: '.swiper-button-next3',

          prevButton: '.swiper-button-prev3',

          slidesPerView: 1,

          paginationClickable: true,

          spaceBetween: 30,

          loop: true
        }
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import "./swiper.css"
    .swiper-button-prev3
      z-index:10000
      position:absolute
      top:40%
      color:rgb(191, 73, 170)
      left:2%
      @media only screen and (max-width: 728px)
        left:0px
    .swiper-button-next3
      position:absolute
      top:40%
      z-index:10000
      color:rgb(191, 73, 170)
      right:0%
      @media only screen and (max-width: 728px)
        right:0
    .swiper-slide
        .mask3
          width:100%
          margin-left:1%
          background:#dedcdc
          height:90%
          position:absolute
          z-index:-1
          border-radius:10px
          margin-top:2%
        .masked3
          width:100%
          margin-left:2%
          background:#e5e5e5
          height:80%
          position:absolute
          z-index:-2
          border-radius:10px
          margin-top:4%
        .slider-list
          position:relative
          width:88%
          margin-left:6%
          margin-right:6%
          margin-bottom:30px
          border:1px solid #fff
          background:#fff
          border-radius:10px
          box-shadow: 4px 8px 20px -2px #bdbaba
          @media only screen and (max-width: 728px)
            width:80%
            margin-left:10%
            margin-right:10%
          .reviews-details
            padding-left:3%
            overflow:hidden
            .reviews-details-top
              padding-top:14px
              .reviews-left-img
                float:left
                width:66px
                height:66px
                border:1px solid #000 
                border-radius:66px
                text-align:center
                line-height:66px
                svg
                  padding-top:10px
              .reviews-name
                float:left
                width:30%
                padding-left:20px
                margin-top:20px
                .reviews-names
                  font-size:20px
                  color:#808080
              .report
                float:right
                padding-right:4%
                width:20%
                text-align:right
                color:#808080
                i
                  padding-top:20px
                  font-size:20px
                  transform: rotate(90deg)
            .reviews-details-bottoms
              clear:both
              padding-top:20px
              color:#8d8d8d
              font-size:20px
              line-height:34px
              padding-right:6%
              padding-bottom:20px
              @media only screen and (max-width: 728px)
                font-size:14px
                line-height:20px
              p:first-child
                color:#696969
                font-weight:bold
            .reviews-details-img
              .img01
                width:90%
                margin-left:10px
                img
                  width:100% 
                  margin-top:10px
            .browse
              margin-top:10px
              margin-bottom:40px
              p
                text-align:right
                padding-right:40px
                span
                  margin-left:6px
                  svg
                    color:rgb(191, 73, 170)
                  
                  
              
                
              
              
</style>


