<template>
  <div class="forum">
  	<div class="content">
  		<div class="forum-top">
	  		<div class="forum-top-left">
	  			<p class="forum-left-img"><img src="./bus.png"></p>
	  			<p class="forum-top-icon">
	  				<i class="el-icon-star-on"></i>
	  				<i class="el-icon-star-on"></i>
	  				<i class="el-icon-star-on"></i>
	  				<i class="el-icon-star-on"></i>
	  				<i class="el-icon-star-off"></i>
	  			</p>
	  			<p class="forum-left-num">4.0</p>
	  		</div>
	  		<div class="forum-top-right">
	  			<p class="forum-right-title">Flying Bus Simulator</p>
	  			<p class="forum-right-detail">Developer:BANDAI NAMCO Entretainment Inc</p>
	  			<p class="forum-right-num"><span>123456</span>人</p>
	  			<p class="forum-right-love">
	  				<icon name="heart"></icon>
	  				<button class="attention">关注</button>
	  				<button class="down" :style="{ background: tabColor}">下载</button>
	  			</p>
	  		</div>

  		</div>
  		<div class="forum-top-tab"> 
  			<div class="tab">
  				<div class="tab-item">
	                <router-link id="details" to="/Forum/details" class="active" v-on:click="jqueryData()" :style="{ background: tabColors , color : color}">详情</router-link>  
			        
			    </div>
		    <div class="tab-item">
                <router-link id="comments" to="/Forum/comments" :style="{ background: tabColor2 , color : color2}">评论</router-link>  
		       
		    </div>
		    <div class="tab-item">
                <router-link id="forums" to="/Forum/forums" :style="{ background: tabColor3 ,  color : color3}">论坛</router-link>  
		    </div>
  			</div>
  			
		    <div>
    			<router-view></router-view>
		    	
		    </div>
  		</div>
  		
  	</div>
  </div>

</template>
<script>

export default {
    data () {
      return {
        activeName: 'first',
        positiveType: 'e',
        jqueryData: function () {
        console.log(3984)
        },
        tabColor: '#e94333',
        tabColors: '#e94333',
        tabColor2: '#fff',
        tabColor3: '#fff',
        color: '#fff',
        color2: '#656565',
        color3: '#656565'

      }
    },
    methods: {
      select (type) {
        this.positiveType = type
        console.log(type)
      }
    },
  watch: {
    '$route' (to) {
      if (to.path === '/Forum/details') {
        this.tabColor = '#e94333'
        this.tabColors = '#e94333'
        this.tabColor2 = '#fff'
        this.tabColor3 = '#fff'
        this.color = '#fff'
        this.color2 = '#656565'
        this.color3 = '#656565'
      }
      if (to.path === '/Forum/comments') {
        this.tabColor = '#fcd400'
        this.tabColors = '#fff'
        this.tabColor2 = '#fcd400'
        this.tabColor3 = '#fff'
        this.color = '#656565'
        this.color2 = '#fff'
        this.color3 = '#656565'
      }
      if (to.path === '/Forum/forums') {
        this.tabColor = '#bf49aa'
        this.tabColors = '#fff'
        this.tabColor2 = '#fff'
        this.tabColor3 = '#bf49aa'
        this.color = '#656565'
        this.color2 = '#656565'
        this.color3 = '#fff'
      }
    }
  }

  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
.forum
	width:100%
	background:#f2f2f2
	padding-top:30px
	padding-bottom:90px
	.content
		.forum-top
			border-bottom:1px solid #e43e32
			background:#fff
			border-radius:16px 16px 0px 0px
			box-shadow: 4px 8px 20px -2px #bdbaba
			overflow:hidden
			padding-bottom:20px
			.forum-top-left
				width:15%
				float:left
				padding-left:2%
				padding-top:2%
				text-align:center
				@media only screen and (max-width: 728px)
					width:26%
				p
					img
						width:100%
				.forum-top-icon
					margin-top:20px
					text-align:center
					font-size:18px
					@media only screen and (max-width: 748px)
						margin-top:4px
					i
						@media only screen and (max-width: 948px)
							font-size:12px
						@media only screen and (max-width: 748px)
							font-size:8px
				.forum-left-num
					text-align:center
					font-size:28px
					color:#f39500
					margin-top:10px
					@media only screen and (max-width: 748px)
						font-size:12px
			.forum-top-right
				margin-left:2%
				width:78%
				float:left
				padding-top:2%
				@media only screen and (max-width: 728px)
					width:67%
					margin-left:3%
				.forum-right-title
					font-size:24px
					color:#656565
					margin-top:6px
					@media only screen and (max-width: 728px)
						font-size:16px
						margin-top:8px
				.forum-right-detail
					font-size:14px
					color:#656565
					margin-top:22px
					@media only screen and (max-width: 728px)
						font-size:12px
				.forum-right-num
					font-size:18px
					color:#656565
					margin-top:26px
				.forum-right-love
					text-align:right
					margin-top:60px
					@media only screen and (max-width: 728px)
						margin-top:14px
					svg
						color:#f15b20
					button
						border-radius:10px
						border:1px solid #e94333
						font-size:28px
						width:108px
						height:60px
						line-height:60px
						text-align:center
						margin-left:10px
						@media only screen and (max-width: 728px)
							font-size:16px
							width:80px
							height:30px
							line-height:30px
					.attention
						background:#fff
						color:#656565
					.down
						background:#e94333
						color:#fff
		.forum-top-tab
			position:relative
			.tab	
				display:flex
				width:100%
				border-radius:0px 0px 16px 16px
				box-shadow: 4px 8px 20px -2px #bdbaba
				background:#fff
				.tab-item
					flex:1
					a
						width:100%
						display:inline-block
						height:96px
						line-height:96px
						text-align:center
						background:#fff
						font-size:28px
						@media only screen and (max-width: 728px)
							height:60px
							line-height:60px
							font-size:20px
				.tab-item:first-child
					a
						border-radius:0px 0px 0px 16px
				.tab-item:last-child
					a
						border-radius:0px 0px 16px 0px		
					
				       
								
								
							
										
									
								

								
								
								
					

									
								
						
					
					
				
						
				
					
					
					
			
</style>





