<template>
  <div class="main">
    <div class="content">
    	<div class="banner">
    		<img src="./mainBanner.jpg">
    	</div>
    	<div class="hotgame">
        <v-comment></v-comment>
        <div class="hotgame-to">
          <v-slider></v-slider>
          <hr>
          <v-slider></v-slider>
          <hr>
          <v-slider3></v-slider3>

        </div>
    	</div>
      <div class="height"></div>
    </div>
  </div>

</template>
<script>
import comment from '../Comment/swiper.vue'
import Slider from '../Slider/Slider.vue'
import Slider2 from '../Slider2/Slider.vue'
import Slider3 from '../Slider3/Slider.vue'

export default {
  name: 'comment',
  components: {
      'v-comment': comment,
      'v-slider': Slider,
      'v-slider2': Slider2,
      'v-slider3': Slider3

    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .main
    background:#f2f2f2
    width:100%
    .content
      .banner
        width:100%
        padding-top:14px
        img
          width:100%
      .hotgame
        .hotgame-to
          border:1px solid #fff
          background:#fff
          border-radius:16px
          box-shadow: 4px 8px 20px -2px #bdbaba
          margin-top:25px
      .height
        height:60px
        width:100%
        clear:both

</style>





