              <div class="header-list">
                <el-collapse-transition>
                  <div v-show="show3" class="show3">
                    <div class="transition-box">
                      <router-link id="Main" to="/" class='main-top mainClass'>Main</router-link>   
                    </div>
                    <div class="transition-box">
                      <router-link id="Hot" to="/Hot" class="hotClass">Hot</router-link>   
                    </div>
                    <div class="transition-box">
                      <router-link id="Reviews" to="/Reviews" class="reviewsClass">Reviews</router-link>   
                    </div>
                    <div class="transition-box">
                      <router-link id="Discover" to="/Discover" class="discoverClass">Discover</router-link>   
                    </div>
                    <div class="transition-box">
                      <router-link id="Forum" to="/Forum" class="forumClass">Forum</router-link>   
                    </div>
                  </div>
                </el-collapse-transition>
              </div> -->
              <el-dropdown>
                <el-button type="">
                  <icon name="bars" scale="2"></icon>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>
                      <router-link id="Main" to="/" class='main-top mainClass'>Main</router-link>  
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <router-link id="Hot" to="/Hot" class="hotClass">Hot</router-link>   
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <router-link id="Reviews" to="/Reviews" class="reviewsClass">Reviews</router-link>
                  </el-dropdown-item>
                  <el-dropdown-item>
                    <router-link id="Discover" to="/Discover" class="discoverClass">Discover</router-link>
                  </el-dropdown-item>
                  <el-dropdown-item>
                     <router-link id="Forum" to="/Forum" class="forumClass">Forum</router-link>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div> 
            <div class="header-brand">
              <a class="navbar-brand" href="">
                <img src="./logo.jpg" alt="TapTap" title="TapTap">
              </a>
            </div>
