            <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" router>
              <el-menu-item index="/" class="mainClass">Main</el-menu-item>
              <el-menu-item index="/Hot" class="hotClass">Hot</el-menu-item>
              <el-menu-item index="/Reviews" class="reviewsClass">Reviews</el-menu-item>
              <el-menu-item index="/Discover" class="discoverClass">Discover</el-menu-item>
              <el-menu-item index="/Forum"  class="forumClass">Forum</el-menu-item>

            </el-menu>
          <!-- </div> -->
          </el-col>
          <el-col :xs="12" :sm="5" :md="5" :lg="5" class="header-right">
            <div class="header-icon">
              <i class="el-icon-search"></i>
              <icon name="language"></icon>
              <icon name="bell"></icon>
              <icon name="user-circle"></icon>
            </div>
          </el-col>
        </div>
        
      </el-row>

      
    </div>
   
  </div>
</template>

<script>
    export default {
    data: () => ({
      show3: false
    })
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="stylus" rel="stylesheet/stylus">
// @media (max-width: 1200px)
// @media print 
  .header
    width: 100%
    height: 54px
    background: #fff
    position:relative
    box-shadow: 0 2px 2px #e0e0e0
    color:#898989
    .header-con
      width: 85%
      margin:0 auto
      padding: 0 20px
      .header-click
        float:left
        width:25%
        line-height:54px
        position:relative
        z-index:100
        @media only screen and (min-width: 778px)
          display:none  
      .el-dropdown
        .el-button
          border:none
          // margin-right:4px
          color:#656565
          padding:10px 0
          
      .header-list
        position:absolute
        margin-left:-100%
        // height:200px
        width:100%
        // margin-left:-100px;
        .show3
          width:200%
        .transition-box
          margin-top:2px
        .main-top
          margin-top:4px
          
      .hidden-xs
        @media only screen and (max-width: 778px)
          display:none
      .header-left
        float:left  
      .header-brand  
        float:left
        width:70%
        .navbar-brand
          img
            width:90px
      .transition-box 
        margin-bottom: 10px;
        width: 200%;
        height: 34px;
        line-height:34px
        border-radius: 4px;
        background-color: #fff;
        text-align: center;
        color: #fff;
        box-sizing: border-box;
        margin-right: 20px;
      .el-menu-demo
        margin-left: 50px
        float:left
        background: transparent
        height: 54px
        .el-menu-item
          border-bottom:none
          background: transparent
          height: 54px
          line-height: 54px
          font-size: 18px
          @media only screen and (max-width: 992px)
            font-size:14px
        .el-menu-item:hover
          border-bottom:0
          // color:#fff
          border-bottom:none
        .el-menu-item.is-active
          border-bottom:none
          color:#fff
          border-bottom:none
        .mainClass.is-active:hover
          background: #e53f31
        .mainClass.is-active
          background: #e53f31
        .hotClass.is-active:hover
          background: #f7ba00
        .hotClass.is-active
          background: #f7ba00
        .reviewsClass.is-active:hover
          background: #b740a4
        .reviewsClass.is-active
          background: #b740a4
        .discoverClass.is-active:hover
          background: #00a2eb
        .discoverClass.is-active
          background: #00a2eb
        .forumClass.is-active:hover
          background: #b740a4
        .forumClass.is-active
          background: #b740a4
    .header-right
      float: right
      .header-icon
        float:right
        i
          font-size:20px
          color:#898989
          text-align:center
          line-height:54px
        svg
          margin-left:10px
.el-dropdown-menu
      width:100%
      a
        display:inline-block
        width:100%
    

</style>



