import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main/Main.vue'
import Hot from '@/components/Hot/Hot.vue'
import tags from '@/components/Hot/tags.vue'
import Reviews from '@/components/Reviews/Reviews.vue'
import Discover from '@/components/Discover/Discover.vue'
import Forum from '@/components/Forum/Forum.vue'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import comments from '@/components/Forum/comments.vue'
import forums from '@/components/Forum/forums.vue'
import details from '@/components/Forum/details.vue'

Vue.use(Router)
Vue.use(VueAwesomeSwiper)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/Hot',
      name: 'Hot',
      component: Hot
    },
    {
      path: '/Reviews',
      name: 'Reviews',
      component: Reviews
    },
    {
      path: '/Discover',
      name: 'Discover',
      component: Discover
    },
    {
      path: '/Forum',
      name: 'Forum',
      component: Forum,
      redirect: '/Forum/details',
      children: [
        {
          path: '/Forum/details',
          component: details
        },
        {
          path: '/Forum/comments',
          component: comments
        },
        {
          path: '/Forum/forums',
          component: forums
        }
      ]
    },
    {
      path: '/tags',
      name: 'tags',
      component: tags
    }
  ]
})




