<template>
  <div class="slider2">
        <swiper :options="swiperOptions">

          <swiper-slide>
            <div class="slider-listes">
              <div class="masks"></div>
              <div class="maskeds"></div>
              <div class="slider-reviews">
                <p class="reviews-to">Reviews</p>
                <p class="reviews-more">More(221)</p>
              </div>
              <div class="reviews-details">
                <div class="reviews-details-top">
                  <div class="reviews-left-img">
                    <img src="./pig.png">
                  </div>
                  <div class="reviews-name">
                    <p class="reviews-names">欧派哥哥の萌大奶</p>
                    <p class="reviews-icon"> 
                      <i class="el-icon-star-on"></i>
                      <i class="el-icon-star-on"></i>
                      <i class="el-icon-star-on"></i>
                      <i class="el-icon-star-on"></i>
                      <span class="time">2017-05-12</span>
                    </p>
                  </div>
                  <div class="report">
                    <span>Report</span>
                  </div>
                </div>
                <div class="reviews-details-bottom">
                  <p>蒋蒋，我又来了......</p>
                  <p>游戏的玩法蛮独特的，玩法上讲究的是“站撸”，看谁的武器好，游戏并不需要太多的策略，也不伤手。适合所有的玩家。</p>
                </div>
              </div>
            </div>
          </swiper-slide>
          
          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
          <div class="swiper-button-prevess" slot="button-prev"><icon name="angle-left" scale="5"></icon></div>
          <div class="swiper-button-nextess" slot="button-next"><icon name="angle-right" scale="5"></icon></div>
        </swiper>
  </div>
</template>

<script>
  // import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {

    data () {
      return {
        swiperOptions: {

          pagination: '.swiper-pagination',

          nextButton: '.swiper-button-nextess',

          prevButton: '.swiper-button-prevess',

          slidesPerView: 1,

          paginationClickable: true,

          spaceBetween: 30,

          loop: true
        }
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import "./swiper.css"
.slider2
     .swiper-button-prevess
      z-index:10000
      position:absolute
      top:40%
      color:rgb(252, 212, 0)
      left:0%
      @media only screen and (max-width: 428px)
        display:none
    .swiper-button-nextess
      position:absolute
      top:40%
      z-index:10000
      color:rgb(252, 212, 0)
      right:0%
      @media only screen and (max-width: 428px)
        display:none
    .swiper-slide
        .masks
          width:100%
          margin-left:1%
          background:#dedcdc
          height:90%
          position:absolute
          z-index:-1
          border-radius:10px
          margin-top:2%
        .maskeds
          width:100%
          margin-left:2%
          background:#e5e5e5
          height:80%
          position:absolute
          z-index:-2
          border-radius:10px
          margin-top:4%
        .slider-listes
          position:relative
          width:88%
          margin-left:6%
          margin-right:6%
          margin-bottom:30px
          border:1px solid #fff
          background:#fff
          border-radius:10px
          box-shadow: 4px 8px 20px -2px #bdbaba
          @media only screen and (max-width: 728px)
            width:80%
            margin-left:10%
          @media only screen and (max-width: 428px)
            width:100%
            margin-left:0px
            margin-right:0px
          .slider-reviews
            border-bottom:2px solid #fcc88f
            overflow:hidden
            .reviews-to
              font-size:28px
              color:#656565
              padding:16px
              padding-left:4%
              float:left
            .reviews-more
              font-size:18px
              color:#dfdddd
              float:right
              padding-top:12px
              padding-right:3%
          .reviews-details
            padding-left:3%
            overflow:hidden
            .reviews-details-top
              padding-top:14px
              .reviews-left-img
                float:left
                width:66px
                height:66px
                border:1px solid #000 
                border-radius:66px
                text-align:center
                line-height:66px
                img
                  width:80%
                  border-radius:66px
                  vertical-align:middle
              .reviews-name
                float:left
                width:30%
                padding-left:20px
                margin-top:4px   
                .reviews-names
                  font-size:20px
                  color:#808080
                  @media only screen and (max-width: 728px)
                    font-size:16px
                .reviews-icon
                  margin-top:15px
                  font-size:12px
                  @media only screen and (max-width: 728px)
                    margin-top:8px
                  i
                    color:#fbb637
                  span
                    margin-left:6%
                    color:#808080
                    @media only screen and (max-width: 728px)
                      margin-left:3%
              .report
                float:right
                padding-right:4%
                width:20%
                text-align:right
                font-size:16px
                color:#808080
            .reviews-details-bottom
              clear:both
              padding-top:20px
              color:#808080
              font-size:20px
              line-height:34px
              padding-right:6%
              padding-bottom:68px
              @media only screen and (max-width: 728px)
                font-size:14px
                padding-bottom:30px
              
              
                  
              
                
              
              
</style>


