<template>
	<div class="footer" :style="{ background: footColor}">

			<p class="copyright">&copy;2017 TuTu.All rights reserved.</p>
			<p class="footer-img">
				<img src="./tutu.png">
			</p>
			<p class="contact">
				<span class="us">Contact Us:</span>
				<ul>
					<li><icon name="facebook"></icon></li>
					<li><icon name="twitter"></icon></li>
					<li><span>in</span></li>
					<li><icon name="youtube-play"></icon></li>

				</ul>
			</p>
	
		
	</div>
</template>
<script>

export default {
  data () {
    return {
    }
  },
  props: {
    footColor: {
      type: String
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.footer
	height:124px
	width:100%
	.copyright
		width:20%
		float:left
		padding-left:4%
		line-height:124px
		font-size:12px
		color:#fff
		text-align:right
	.footer-img
		width:36%
		float:left
		text-align:center
		padding-top:45px
	.contact
		width:20%
		padding-right:4%
		color:#fff
		float:right
		.us
			display:block
			padding-top:30px
			font-size:12px
			padding-bottom:20px
		ul
			li
				display:inline-block
				float:left
				margin-right:30px
				@media only screen and (max-width: 878px)
					margin-right:10px
				@media only screen and (max-width: 478px)
					margin-right:4px
				
        		
				
				
	
</style>



