<template>
  <div id="app">
    <v-header></v-header>
    <router-view></router-view>
    <div class="clear"></div>
    <v-footer :footColor="footColor"></v-footer>

  </div>
</template>

<script>
import header from './components/Head/Head.vue'
import footer from './components/Footer/Footer.vue'
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'

export default {
  name: 'app',
  data () {
    return {
      footColor: '#e53f31'
    }
  },
  components: {
      'v-header': header,
      'v-footer': footer,
      'Icon': Icon
  },
  created () {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    if (this.$route.path === '/') {
      this.footColor = '#e53f31'
    }
    if (this.$route.path === '/Hot') {
      this.footColor = '#f7ba00'
    }
    if (this.$route.path === '/Reviews') {
      this.footColor = '#b740a4'
    }
    if (this.$route.path === '/Discover') {
      this.footColor = '#00a2eb'
    }
    if (this.$route.path === '/Forum') {
      this.footColor = '#b740a4'
    }
  },
  watch: {
    '$route' (to) {
      if (to.path === '/') {
        this.footColor = '#e53f31'
      }
      if (to.path === '/Hot') {
        this.footColor = '#f7ba00'
      }
      if (to.path === '/Reviews') {
        this.footColor = '#b740a4'
      }
      if (to.path === '/Discover') {
        this.footColor = '#00a2eb'
      }
      if (to.path === '/Forum') {
        this.footColor = '#b740a4'
      }
    }
  }
}
</script>




