<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  data () {
    return {
      msg: 'Hot Game',
      swiperOptiones: {
        pagination: '.swiper-pagination',
        // slidesPerView: 'auto',
        paginationClickable: true,
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        // spaceBetween: 5
        // freeMode: true
        slidesPerView: 6,
        // spaceBetween: 30,
        breakpoints: {
          1320: {
            slidesPerView: 6
            // spaceBetween: 40
          },
          1215: {
            slidesPerView: 4
            // spaceBetween: 40
          },
          866: {
            slidesPerView: 3
            // spaceBetween: 30
          },
          645: {
            slidesPerView: 1
            // spaceBetween: 20
          },
          320: {
            slidesPerView: 1
            // spaceBetween: 10
          }
        }
      }
    }
  },
  components: {
    swiper,
    swiperSlide
  }
}
</script>
