<template>
  <div>
    <!-- <h1>{{ msg }}</h1> -->
    <div class="chart" >
      <div class="mianChart" id="main"></div>
    </div>
  </div>
</template>

<script>
let echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/title')
require('echarts/lib/component/legend')

export default {
  data () {
    return {
      msg: 'This is Comment'
    }
  },
  created () {
    this.$nextTick(() => {
      var myChart = echarts.init(document.getElementById('main'))
      myChart.setOption({
        color: ['#E33628', '#F8AE01', '#B13E93', '#01A0EA', '#87C442'],
        legend: {
          orient: 'vertical',
          left: '23%',
          top: 'center',
          align: 'right',
          itemWidth: 12,
          itemHeight: 12,
          icon: 'path://M506.304 66.944c3.136-6.336 8.192-6.336 11.328 0l137.024 277.568c3.136 6.336 11.392 12.288 18.304 13.312l306.368 44.544c6.976 1.024 8.512 5.888 3.52 10.752l-221.696 216.064c-5.056 4.928-8.192 14.592-6.976 21.568l52.352 305.088c1.216 6.912-2.944 9.92-9.152 6.656l-273.984-144.064c-6.208-3.264-16.448-3.264-22.656 0L226.688 962.56c-6.208 3.264-10.368 0.256-9.152-6.656l52.352-305.088c1.216-6.912-1.984-16.64-6.976-21.568L41.152 413.184C36.096 408.256 37.696 403.392 44.672 402.368l306.368-44.544c6.976-1.024 15.232-6.976 18.304-13.312L506.304 66.944z',
          data: ['5', '4', '3', '2', '1']
        },
        title: {
          text: '4.0',
          textStyle: {
            color: '#788288',
            fontStyle: 'normal',
            fontWeight: '300',
            fontSize: 34
          },
          left: 'center',
          top: 'center'
        },
        series: [{
          type: 'pie',
          radius: ['58%', '80%'],
          label: {
            normal: {
              show: false
            }
          },
          data: [{name: '5', value: 90}, {name: '4', value: 9}, {name: '3', value: 9}, {name: '2', value: 2}, {name: '1', value: 2}
          ],
          itemStyle: {
            emphasis: {
              borderWidth: 1,
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }]
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
.chart
  width: 600px
  height: 180px
  background: #fff
  margin:20px auto
  .mianChart
    width: 100%
    height: 100%
</style>



